<template>
  <el-collapse-transition>
    <div class="userSlide" v-show="slideshow" :class="mode">
      <table class="myTable">
        <tr>
          <td class="shuli" @click.prevent.stop="logout">登出</td>
          <td class="shuli">设置</td>
          <td class="shuli" @click.prevent.stop="$Buse.$emit('togglewater')">切换水特效</td>
        </tr>
      </table>
      <div class="shade" @click.stop.prevent="close"></div>
    </div>
  </el-collapse-transition>
</template>

<script>
export default {
  name: "userSlide",
  data() {
    return {
      slideshow: false
    };
  },
  created() {
    this.$Buse.$on("toggleUS", () => {
      this.slideshow = !this.slideshow;
    });
  },
  props: {
    mode: {
      type: String,
      default: "PE"
    }
  },
  methods: {
    close() {
      this.slideshow = false;
    },
    logout() {
      this.close();
      this.$Buse.$emit("logout");
    }
  }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/index.scss";

.userSlide {
  @include background($background-color-theme);
  border: 3px solid transparent;
  border-image: url("../../assets/imgs/border0.png") 50 80 50 80 stretch;
  & .myTable {
    & .shuli {
      padding: 3px;
      width: 20px;
      line-height: 1;
      @include font1_20;
      text-align: justify;
      text-justify: inter-character;
      z-index: 100;
      border-right: 1px solid rgb(22, 22, 22);
      cursor: pointer;
      &:hover {
        background-image: url();
      }
    }
    & tr td:last-child {
      border-right: none;
    }
  }
  & .shade {
    @include divfix;
    z-index: -1;
  }
}
.PE {
  position: absolute;
  right: -4px;
  top: 54px;
}
.PC {
  position: absolute;
  right: 25px;
  top: 64px;
}
</style>
