<template>
  <el-collapse-transition>
    <div class="routeSlide" v-show="menushow">
      <el-menu
        :default-active="this.$store.getters.getActiveIndex"
        mode="vertical"
        @select="handleSelect"
      >
        <el-menu-item index="home">
          首页
          <img class="border" src="../../assets/imgs/timg.png" alt />
        </el-menu-item>
        <el-menu-item index="helloworld">
          书架
          <img class="border" src="../../assets/imgs/timg.png" alt />
        </el-menu-item>
        <el-menu-item index="blog">
          blog
          <img class="border" src="../../assets/imgs/timg.png" alt />
        </el-menu-item>
        <el-menu-item index="chatroom" v-show="this.$store.getters.getLoginStatus">
          聊天室
          <img class="border" src="../../assets/imgs/timg.png" alt />
        </el-menu-item>
        <el-menu-item index="chain">
          友链
          <img class="border" src="../../assets/imgs/timg.png" alt />
        </el-menu-item>
        <el-menu-item index="about">
          关于
          <img class="border" src="../../assets/imgs/timg.png" alt />
        </el-menu-item>
      </el-menu>
      <div class="bg" @click="close"></div>
    </div>
  </el-collapse-transition>
</template>

<script>
export default {
  data() {
    return {
      menushow: false
    };
  },
  created() {
    this.$Buse.$on("toggleRS", () => {
      this.menushow = !this.menushow;
    });
  },
  methods: {
    handleSelect(key) {
      this.$store.commit("setActiveIndex", key);
      switch (key) {
        case "home":
          this.$router.push("/");
          break;
        case "chatroom":
          this.$router.push("/chatroom");
          break;
        case "helloworld":
          this.$router.push("/helloworld");
          break;
        case "blog":
          this.$router.push("/blog");
          break;
        case "about":
          this.$router.push("/about");
          break;
        case "chain":
          this.$router.push("/chain");
      }
      this.menushow = false;
    },
    close() {
      this.menushow = false;
    }
  }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/index.scss";

.routeSlide {
  & .el-menu {
    width: 150px;
    @include background($background-color-theme);
    border: 5px solid transparent;
    text-align: center;
    border-image: url("../../assets/imgs/border0.png") 50 80 50 80 stretch;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    z-index: 100;
    & .el-menu-item {
      @include font1_20;
      & .border {
        width: 100px;
        height: 10px;
        position: absolute;
        bottom: 0;
        left: 0;
        opacity: 0;
        transform: translateX(50px);
        transition: all 0.25s linear;
      }
      &:not(.is-disabled):focus,
      &:not(.is-disabled):hover,
      &.is-active {
        background: transparent;
        @include font1_24;
        @include color($font-color-theme);
        border: none;
        & .border {
          transition: all 0.25s linear;
          transform: translateX(25px);
          opacity: 1;
        }
      }
    }
  }
  & .bg {
    @include divfix;
  }
}
</style>
