<template>
  <div class="Main">
    <Navbar />
    <transition :name="transitionName" mode="out-in">
      <router-view class="scroll" />
    </transition>
    <el-backtop target=".Main" :right="20" :bottom="100">
      <div
        style="{
        height: 100%;
        width: 100%;
        text-align: center;
        line-height: 40px;
        color: #000;
      }"
      >
        <span class="icon el-icon-arrow-up"></span>
      </div>
    </el-backtop>
  </div>
</template>

<script>
import Navbar from "@/views/Navbar.vue";
import copyright from "../components/copyright.vue";
export default {
  data() {
    return {
      // url: "/api/imgs/webbg3.jpg",
      wateropen: true,
      transitionName: "slide-right"
    };
  },
  components: { Navbar, copyright },
  mounted() {
    // this.ripple();
    this.$Buse.$on("togglewater", () => {
      if (this.wateropen) {
        this.waterhide();
      } else {
        this.watershow();
      }
    });
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to.matched.length >= 2) {
        vm.$store.commit("setActiveIndex", to.matched[1].path.slice(1));
      }
    });
  },
  beforeDestroy() {
    // this.waterdestroy();
  },
  methods: {
    ripple() {
      this.$nextTick(() => {
        $(".Main").ripples({
          resolution: 500,
          dropRadius: 5, //px
          perturbance: 0.04
        });
      });
    },
    watershow() {
      $(".Main").ripples("show");
      this.wateropen = true;
    },
    waterhide() {
      $(".Main").ripples("hide");
      this.wateropen = false;
    },
    waterdestroy() {
      $(".Main").ripples("destory");
    }
  }
};
</script>

<style scoped lang="scss">
.Main {
  position: relative;
  width: 100%;
  height: 100%;
  /* background-image: url("/api/imgs/webbg3.jpg"); */
  background-image: url("../assets/imgs/bg3.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  transition: all 1s linear;
  overflow-y: scroll;
  padding-top: 60px;
  box-sizing: border-box;
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  transform: translate(50px, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  transform: translate(-50px, 0);
}
</style>
